import React, { useState, useEffect, useContext } from 'react';
import { createBottomTabNavigator} from "@react-navigation/bottom-tabs";
import { View,Text, Button } from 'react-native';
import { AuthContext } from './AuthProvider';
import {Ionicons } from '@expo/vector-icons';
import { MaterialCommunityIcons } from '@expo/vector-icons';
import axios from 'axios';
import basic_url from './baseUrl';
import { HomeStack } from './HomeStack';
import { TouchableOpacity } from 'react-native-gesture-handler';

const Tabs = createBottomTabNavigator();


function Search(){
    return (
        <View>
            <Text>I am at Search</Text>
        </View>
    )
}

function Notification(){
  return (
      <View>
          <Text>I am at Notification</Text>
      </View>
  )
}

function Profile(){

  return (
      <View>
          <Text>I am at Profile</Text>
      </View>

  )
}

export const AppTabs= ({}) => {
  const {logout} = useContext(AuthContext);
    return (
        <Tabs.Navigator screenOptions={({ route }) => ({
            tabBarIcon: ({ focused, color, size }) => {
              let iconName;
  
              if (route.name === 'Home') {
                iconName = 'md-home';
              } else if (route.name === 'Search') {
                iconName = 'md-search';
              }
              else if (route.name === 'Notification') {
                iconName = 'md-search';
                return <Ionicons name="ios-notifications-outline" size={size} color={color} />
              }
              else if (route.name === 'Profile') {
                iconName = 'md-search';
                return <MaterialCommunityIcons name="account-card-details-outline" size={size} color={color} />
              }
  
              // You can return any component that you like here!
              return <Ionicons name={iconName} size={size} color={color} />;
            },
          })}
          tabBarOptions={{
            activeTintColor: 'tomato',
            inactiveTintColor: 'gray',
          }}>
            <Tabs.Screen name="Home" component={HomeStack}></Tabs.Screen>
            <Tabs.Screen name="Search" component={Search}></Tabs.Screen>
            <Tabs.Screen name="Notification" component={Notification}></Tabs.Screen>
            <Tabs.Screen name="Profile" component={Profile}></Tabs.Screen>
        </Tabs.Navigator>

    );
}