function GetSubjects({ navigation }){
    const {estudyToken, logout} = useContext(AuthContext);
    const [data, setData] = useState([]);

    useEffect(() => {

        const token = "Token " + estudyToken;

        axios.defaults.headers = {
            "Content-Type": "application/json",
            Authorization: token
        }
        axios.get(basic_url + 'course/').then(res => {
            setData(res.data);
            
        }).catch(err => {
            console.log(err);
            logout();
        })

        setData(Data)

    },[])

    return(
        <View style={styles.container}>
        <FlatList
          style={{flex:1}}
          data={data}
          renderItem={({ item }) => <Item item={item} navigation = {navigation}/>}
          keyExtractor={item => item.id.toString()}
        />
      </View>
    )
    
}

function Assignments({route, navigation}){
    const [data, setData] = useState([]);
    const {estudyToken, logout} = useContext(AuthContext);

    useEffect(() => {
        const token = "Token " + estudyToken;

        axios.defaults.headers = {
            // "Content-Type": "application/json",
            Authorization: token,
            'Accept': 'application/json',
            'Content-Type': 'multipart/form-data'
        }
        axios.get(basic_url + 'assignments/'+route.params.object.id).then(res => {
            setData(res.data);
            console.log(res.data);
            
        }).catch(err => {
            console.log(err.code);
            logout();
        })

        setData(Assignemtss);

    },[])
    
    return(
        <View style={styles.container}>
        <FlatList
          style={{flex:1}}
          data={data}
          renderItem={({ item }) => <AssignmentsList item={item} navigation = {navigation}/>}
          keyExtractor={item => item.id.toString()}
        />
      </View>
    )
}


