import React, { Children, useState } from 'react';
import { AsyncStorage } from 'react-native';
import basic_url from './baseUrl';
import axios from 'axios';
import { NavigationContainer } from '@react-navigation/native';
export const AuthContext = React.createContext({})


export const AuthProvider = ({children})=>{
    const [estudyToken, setUser] = useState(null);
    const [error, setError] = useState(null);
    
    return(
        <AuthContext.Provider value={{
            estudyToken,
            error,
            login:(username, password) =>{
                if (username && password){


                    axios.defaults.headers = {
                        'Accept': 'application/json',
                        'Content-Type': 'multipart/form-data',
                    }
                    axios.post('https://192.168.0.19:8000/api/login/', {
                        'username': username,
                        'password': password
                    }).then(res => {
                        const currentuser = res.data.token;
      
                        setUser(currentuser);
                        AsyncStorage.setItem('estudyToken', currentuser);
                        setError(false);
                    }).catch(err => {
                        console.log(err);
                        setError(true);
                    })
                }
            },
            logout:()=>{
                setUser(null);
                AsyncStorage.removeItem('estudyToken');
            },
            checkUserStatus:()=>{
                 // check user is loged in
                AsyncStorage.getItem('estudyToken').then(userString =>{
                    if(userString){
                        setUser(userString);
                    } 
                }).catch(err => {
                    console.log(err);
                    setUser(null);
                    AsyncStorage.removeItem('estudyToken');
                })
            }
        }}>
            {children}
        </AuthContext.Provider>
    )
}